import { useState } from 'react';
import './App.css';
// import './dashboard.js';
// import Dashboard from './dashboard.js';
import ColorPalette from './ColorsPalette'
import Poketmon from './Poketmon';
import axios from 'axios';
import {Routes, Route, Link, useNavigate, Outlet} from 'react-router-dom'


//
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Navbar, Nav, InputGroup, Form, Button } from 'react-bootstrap';

function App() {
  const navigate = useNavigate();
  const [colors, setColors] = useState([]);
  const [number,setNumber] = useState(5)
  const getRandomHex = () => {
    const letters = '0123456789ABCDEF';
    let color = '';
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  };
  const axiosColors = ()=>{
    const randomHex = getRandomHex();
    axios.get(`https://www.thecolorapi.com/scheme?hex=${randomHex}&mode=analogic&count=${number}`)
    .then((result)=>{
      console.log(result.data.colors)
      setColors(result.data.colors);
    })
    .catch(()=>{
      console.log('error');
    })
  }
  
  const [tab, setTab] = useState(0);

  return (
    <div className="App">
      <Routes>
        <Route path="/" element={
          <>
          <Navbar bg="white" data-bs-theme="white">
            <Container className='topNavbar'>
              <Nav className="me-auto, topNav">
                <Nav.Link className='textBold nonTextDeco' onClick={()=>{setTab(0)}}>5  </Nav.Link>
                <Nav.Link className='nonTextDeco' onClick={()=>{setTab(1)}}> </Nav.Link>
                <Nav.Link className='nonTextDeco' onClick={()=>{setTab(2)}}>Shop</Nav.Link>
              </Nav>
            </Container>
          </Navbar>
            <article className=''>
              <TabContent tab={tab} axiosColors={axiosColors} number={number} setNumber={setNumber} colors={colors}/>
              
            </article>
          </>
        }>
        </Route>
      </Routes>
    </div>
  );
}

function Dashboard({colors = []}){
  return(
    <>
      <div className="color-palette">
      {colors.map((color, index) => (
        <div key={index} className="color-block" style={{ backgroundColor: color.hex.value }}>
          <p>{color.hex.value}</p>
        </div>
      ))}
    </div>
    </>
  )
}
function FirstArticle({axiosColors, number,setNumber, colors}) {
  return (
    <>
      <ColorPalette axiosColors={axiosColors} number={number} setNumber={setNumber}></ColorPalette>
      <Dashboard colors={colors}/>
    </>
  )
}
function TabContent(props) {
  if(props.tab == 0) {
    return <FirstArticle axiosColors={props.axiosColors} number={props.number} setNumber={props.setNumber} colors={props.colors} />
  } else if (props.tab == 1) {
    return <Poketmon/>
  } else if (props.tab == 2) {
    return  (<div>2</div>)
  }
}

export default App;
