

function ColorsPalette(props) {
  return(
    <div>
      <h1>5  </h1>
      <div>
        <button disabled={props.number === 1 ? true : false} onClick={()=>{props.setNumber(1)}}>1</button>
        <button disabled={props.number === 3 ? true : false} onClick={()=>{props.setNumber(3)}}>3</button>
        <button disabled={props.number === 5 ? true : false} onClick={()=>{props.setNumber(5)}}>5</button>
        <button disabled={props.number === 7 ? true : false} onClick={()=>{props.setNumber(7)}}>7</button>
      </div>
      <button onClick={props.axiosColors}> !</button>
    </div>
  )
}

export default ColorsPalette;